                  ¥<span class="shop_addmoney1">{{(food.specfoods[0].price*food["is_featured"]).toFixed(1)}}</span>
                </span>
                <span class="shop_addbtn">
                  <span class="shop_addbtn1">
                    <a class="shop_minusclick shop_bluebtn" @click="minusCart2(food)"></a>
                    <span class="shop_many">{{food["is_featured"]}}</span>
                    <a class="shop_addclick shop_bluebtn" @click="addCart2(food)"></a>
                  </span>
                </span>
              </li>
            </ul>
            <div class="shop_adddown">
              <span class="shop_eatbox">餐盒</span>
              <span class="shop_boxmany">{{totalCount}}</span>
            </div>
          </div>
        </div>
        
      </div>
      <div class="shop_db">
        <span class="shop_span00" @click="slide" :class="{'highlight':totalCount>0}">
          <i class="shop_lcar" :class="{'highlight':totalCount>0}"></i>
          <span class="shop_num" v-show="totalCount>0">{{totalCount}}</span>
        </span>
        <div class="shop_pei">
          <p :class="{'highlight':totalPrice>0}">¥<span>{{totalPrice.toFixed(1)}}</span></p>
          <span class="shop_psf">另需配送费￥{{deliveryPrice}}元</span>
        </div>
        <div class="shop_qisong" :class="payClass" @click="gonext">
          {{payDesc}}
        </div>
      </div>
    </div>

    <!-- 遮罩效果 -->
    <div class="shop_shade" v-show="isShow===0" @click="slide">
    </div>
      <div class="shop_xuanfu">
        <div class="shop_consult">
          <img src="https://fuss10.elemecdn.com/d/6d/e90f27add9019b9fc72e4ebbf0959jpeg.jpeg?imageMogr/format/webp/thumbnail/!140x140r/gravity/Center/crop/140x140/" alt="">
          <p>3荤+2素+米饭  注：图片仅供参考，以当天实物为准</p>
        </div>
        <div class="shop_combo">
          <div class="shop_zz">皮蛋瘦肉粥</div>
          <div class="shop_aa">月售478份好评率100%</div>
          <strong>¥<span>19</span></strong>
          <span class="shop_addbtn1 shop_jiajia">
                    <a href="###" class="shop_minusclick shop_bluebtn"></a>
                    <span class="shop_many">1</span>
                    <a href="###" class="shop_addclick shop_bluebtn"></a>
          </span>
        </div>
      </div>
    
  </div>
  </keep-alive>
</template>


<script>
import Vue from "vue"

export default {
  name: 'shop',
  data () {
    return {
      isAct:0,
      isShow:1,
      yuemain:[],
      isOut:0,
      deliveryPrice:5,
      minPrice:20,
      orderList:[]
    }
      
  },
  computed:{
    totalPrice() {
        let total = 0;
        for(var food of this.orderList){
          total+=food.specfoods[0].price*food["is_featured"];
        }
        return total;
      },
      totalCount(){
        let count=0;
        for(var food of this.orderList){
          count+=food["is_featured"];
        }
        return count;
      },
      payDesc(){

        if(this.totalPrice===0){
          return `¥${this.minPrice}起送`;
        }else if(this.totalPrice<this.minPrice){
          let diff=this.minPrice-this.totalPrice;
          return `差¥${diff.toFixed(1)}元起送`;
        }else{
          return '去结算';
        }

      },
      payClass(){
        if (this.totalPrice<this.minPrice) {
          return "not_enough";
        }else{
          return "enough";
        }
      }
  },
  created(){
    bus.$emit("isHide",true);
    this.$http("../../static/yuemain.json").then((res)=>{
        this.yuemain=res.data;
       var orderList=this.$store.state.orderList;

        for(var n=0;n<orderList.length;n++){
            for(var m=0;m<this.yuemain.length;m++){
              for(var i=0;i<this.yuemain[m].foods.length;i++){
                if(orderList[n].name===this.yuemain[m].foods[i].name){
                    this.yuemain[m].foods[i]["is_featured"]=orderList[n]["is_featured"];
                }
                
              }
            }
        }

    });
    this.orderList=this.$store.state.orderList;
  },
  methods:{
    goback(){
        window.history.back();
        bus.$emit("isHide",false);
    },
    change(buer){
      this.isAct=buer;
      this.isOut=buer;
    },
    slide(){
      if(this.totalCount>0){
        this.isShow==1?this.isShow=0:this.isShow=1;
        $('html').css('overflow','hidden');  
      }
    },
    gonext(){
      if(this.totalPrice>=20){
        if(this.$store.state.username.length>0){
            this.$router.push("/count")
        }else{
          this.$router.push("/login")
        }
      }
    },
    addCart(item,e){
      item["is_featured"]++;
      this.$store.dispatch("add_Pro",item);
    },
    minusCart(item){
      if(item["is_featured"]>0){
        item["is_featured"]--;
        this.$store.dispatch("minus_Pro",item);
      }
    },
    addCart2(item){
      var orderList=this.$store.state.orderList;
      for(var i=0;i<orderList.length;i++){
        if (orderList[i].name===item.name) {
          orderList[i]["is_featured"]++;
        };
      }
    },
    minusCart2(item){

      var orderList=this.$store.state.orderList;
      for(var i=0;i<orderList.length;i++){
        if (orderList[i].name===item.name) {
          if (orderList[i]["is_featured"]>0) {
              orderList[i]["is_featured"]--;
              if(orderList[i]["is_featured"]==0){
                orderList.splice(i,1);
                if(orderList.length==0){
                  this.isShow=1
                }
              }
          };
        };
      }
    },
    empty(){
      // this.$store.state.orderList=[];
      for(var n=0;n<this.yuemain.length;n++){
        for(var m=0;m<this.yuemain[n].foods.length;m++){
          this.yuemain[n].foods[m]["is_featured"]=0;
        }
      }
      this.$store.state.orderList.length=0;
      this.orderList.length=0;
      this.isShow=1
    }

  },
  mounted(){
      //主体左半部分的滑动背景色更换的效果
    $('.shop_left li').on('click',function(){
      $(this).addClass("s_active").siblings("li").removeClass("s_active");
       $(".shop_right").scrollTop($("[index="+$(this).index()+"]")[0].offsetTop);
    });

    $(".shop_right").on("scroll",function(){
      var hf=parseInt($("html").css("fontSize"));
      var h=$(".shop_right").scrollTop();
      h=(hf/75)*h;
      var all=$('.shop_left li');
      function change(obj){
        obj.css({"background":"#fff"}).siblings("li").css({"background":" #f8f8f8"});
      }
      switch (true){
        case h>=0&&h<2355:
          change(all.eq(0))
          break;
        case h>2355&&h<2875:
          change(all.eq(1))
          break;
        case h>2875&&h<3165:
          change(all.eq(2))
          break;
        case h>3165&&h<4830:
          change(all.eq(3))
          break;
        case h>4830&&h<6272:
          change(all.eq(4))
          break;
        case h>6272&&h<8170:
          change(all.eq(5))
          break;
        case h>8170&&h<9600:
          change(all.eq(6))
          break;
        case h>9600&&h<10592:
          change(all.eq(7))
          break;
        case h>10592&&h<12487:
          change(all.eq(8))
          break;
        case h>12487&&h<13237:
          change(all.eq(9))
          break;
        case h>13237&&h<14675:
          change(all.eq(9))
          break;
      }
    });
  }

}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@import '../assets/Css/Shop.css'
</style>


