import Vue from "vue"
import Vuex from "vuex"
// Vuex是自定义的
Vue.use(Vuex);


// 这儿保存所有的数据，公有的数据
// 如果是基本数据类型，在子组件内部应该用computed监听，而如果state里面的应用数据类型传到下一级，可以直接在组件内部的data内部直接赋值
const state={
	date:new Date()+"",
	num:0,
	// 订单列表
	orderList:[],
	done:[],
	notdone:[],
	cancel:[],
	username:[],
	time:"15:00",
	timer:""
};

// 同步修改state中数据的方法
// 如果代码中没有异步的代码可以直接调用mutation中的方法
const mutations={
	// 同步修改state中的数据
	// 第一个参数必须是state,可能会有其他参数
	setDate(state){
		state.date=new Date()+"";
	},
	addNum(state){
		state.num++;
	},
	addPro(state,pro){
		var flag=true;
		for(var n=0;n<state.orderList.length;n++){
			if(pro.name===state.orderList[n].name){
				state.orderList.splice(n,1,pro);
				flag=false;
			}
		}
		if(flag){
			state.orderList.push(pro);
		}
	},
	minusPro(state,pro){

		for(var n=0;n<state.orderList.length;n++){

			if(pro.name===state.orderList[n].name){
				state.orderList.splice(n,1,pro);
				if (pro["is_featured"]===0) {
					state.orderList.splice(n,1);
				};
			}
		};

	},
	setTime(state,pro){
		var count=900;
		if(pro===1){
			clearInterval(state.timer);
			  state.timer=setInterval(changenum,1000);
			  function changenum(){
			    count--;
			    if(Math.floor(count/60)>=10){
			      if(count%60>=10){
			        state.time=Math.floor(count/60)+":"+count%60;  
			      }else{
			       	state.time=Math.floor(count/60)+":0"+count%60;
			      }
			    }else{
			         if(count%60>=10){
			            state.time="0"+Math.floor(count/60)+":"+count%60;  
			          }else{
			            state.time="0"+Math.floor(count/60)+":0"+count%60;
			          }
			    }
			  }
		}else if(pro===0){
			clearInterval(state.timer);
		}
	}
};
// 调用mutattions中的方法，修改state数据的方法，比如mutation中接受一个方法，actions是可以重复的调用该方法修改state中的数据
const actions={
	// 可以用一部请求操作，它里操控mutations，mutaition操控state数据
	// 第一个参数必须是store对象
	set_Date(store){
		setInterval(function(){
			// actions调用mutation中的方法，commit,触发函数名加引号；可是谁调用actions呢，外部的组件开放调用
			store.commit("setDate");

		},1000)

	},
	// 函数的解构，当一个对象作为参数时，不用写store对象名，直接写对象中的key名，函数内部可以直接使用该键名
	add_Num({commit}){
		commit("addNum")
	},
	add_Pro({commit},pro){

		commit("addPro",pro);
		// 函数柯里化， 第一个形参被锁定，第二个参数才是真正的参数
	},
	minus_Pro({commit},pro){
		commit("minusPro",pro)
	},
	set_Time({commit},pro){
		commit("setTime",pro)
	}
};
const getters={
// 可以理解为一个计算属性
	total(state){
		var total=0;
		for(var n=0;n<state.orderList.length;n++){
			total+=state.orderList[n]["is_featured"]*state.orderList[n].specfoods[0].price;
		}
		return total;
	}
};
export default new Vuex.Store({
	state,mutations,actions,getters
});
// 如果项目大，store里面的东西可以拆成模块


