<template>

  <div style="height: 100vh;background: #fff;">
        <!-- h -->
        <div class="b_header" @click="goback">
            <span></span>
            <p>购物车</p>
        </div>
        <!-- 主体 -->
        <div class="b_main" v-show="isShow===0">
            <div class="b_lgxz">
                <div class="b_div1">
                    <span class="b_span1">粤粥坊</span>
                    <span class="b_span2"></span>
                    <span class="b_span3"></span>
                </div>
                <div class="b_div2" v-for="(food,index) in orderList" :key="index">
                    <div class="b_div2-1">
                        <div class="b_slxy">
                            <img :src="'https://fuss10.elemecdn.com/'+food.image_path+'.jpeg?imageMogr/format/webp/thumbnail/!140x140r/gravity/Center/crop/140x140/'" alt="">
                        </div>
                        <div class="b_sx">
                                <div class="b_xy"><span>{{food.name}}</span><a @click="del(food)">×</a></div>
                                <div class="b_jiage"><span>x{{food["is_featured"]}}</span><a class="a2">{{(food.specfoods[0].price*food["is_featured"]).toFixed(1)}}</a><a class="a1">¥</a></div>
                        </div>
                    </div>
                </div>
                <div class="b_div3">
                    <span>在线支付立减优惠</span><a>- ¥5</a>
                </div>
                <div class="b_div4">
                    <span class="b_span20">优惠<a href="">5</a>元</span>
                    <p>合计<a href="###">¥{{total-5}}</a><span class="span23" @click="goCount">去结算</span></p>
                </div>
            </div>
        </div>
        <div class="boxempty" v-show="isShow===1">
            <section> 
                <img src="https://fuss10.elemecdn.com/2/50/8019fbaebac2aaa76e2d9b5e5b407gif.gif"> 
                <h3>购物车是空的</h3>
            </section>  
        </div>
    </div>
</template>

<script>
export default {
  name: 'box',
  data () {
    return {
        orderList:[],
        isShow:1
    }
  },
  created(){
    bus.$emit("isHide",true);
    this.orderList=this.$store.state.orderList;
    this.orderList.length>0?this.isShow=0:this.isShow=1;
  },
  methods:{
    goback(){
        window.history.back();
        bus.$emit("isHide",false);
    },
    del(item){
      var orderList=this.$store.state.orderList;
      for(var i=0;i<orderList.length;i++){
        if (orderList[i].name===item.name) {
          orderList.splice(i,1);
          if (orderList.length==0) {
            this.isShow=1;
          };
        };
      }
    },
    goCount(){
      if(this.$store.state.username.length>0){
            this.$router.push("/count")
        }else{
          this.$router.push("/login")
        }
    }
  },
  computed:{
    total(){
        return this.$store.getters.total
    }
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@import '../assets/Css/Box.css'
</style>


