<template>
  <div class="share_box">
    <!-- h -->
    <div class="share_header">
      <span @click="goback"></span>
      <p>分享有奖</p>
    </div>
    <!-- 1 -->
    <div class="share_main1">
      <!-- o1 -->
      <div class="share_main-1">
        <img src="https://fuss10.elemecdn.com/6/33/1b7d02185816cb1a6e079acc779c3png.png " alt="">
        <span class="share_span1">规则></span>
      </div>
      <!-- 02 -->
      <div class="share_main-2">
        <div class="share_p1">再邀请<em>2人</em>，得到额外奖励<em>5元</em></div>
      </div>
    <!-- 03 -->
      <div class="share_hongbao" v-show="isShow===1">
        
        <div class="share_pac1" style="left:10%">
          <img src="../assets/calai.png" alt="">
          <div class="share_qian">
            <em>5</em>
            <span>元</span>
          </div>
         </div>
        <div class="share_pac1" style="left:54%">
          <img src="../assets/calai.png" alt="">
          <div class="share_qian">
            <em>10</em>
            <span>元</span>
          </div>
         </div>
         <div class="share_pac1" style="left:97%">
          <img src="../assets/calai.png" alt="">
          <div class="share_qian">
            <em>20</em>
            <span>元</span>
          </div>
         </div>
        <div class="share_jindu">
          <div class="share_dian" style="left: 7%;"></div>
          <div class="share_dian" style="left: 52%;"></div>
          <div class="share_dian" style="left: 95%;"></div>
        </div>
      </div>
      <!-- 04 -->
      <!-- 社会化分享 -->
      <div id="s_box">
        <div class="-mob-share-ui-button -mob-share-open">分享领红包</div>
        <div class="-mob-share-ui" style="display: none">
            <ul class="-mob-share-list">
                <li class="-mob-share-weibo"><p>新浪微博</p></li>
                <li class="-mob-share-qzone"><p>QQ空间</p></li>
                <li class="-mob-share-qq"><p>QQ好友</p></li>
                <li class="-mob-share-douban"><p>豆瓣</p></li>
                <li class="-mob-share-facebook"><p>Facebook</p></li>
                <li class="-mob-share-twitter"><p>Twitter</p></li>
            </ul>
            <div class="-mob-share-close">取消</div>
        </div>
      </div>
        <!-- 2 -->
      <div class="share_shouyi">
          <h3 class="share_h32"><span class="share_span9"></span>我的受益</h3>
        <div class="share_renyuan">
          <div class="share_o1">
            <span class="share_u1">0</span class="share_u2"><span>元</span>
            <p>预计收益</p>
          </div>
          <div class="share_o1">
            <span class="share_u3">0</span class="share_u2"><span>元</span>
            <p>预计收益</p>
          </div>
          <div class="share_o1">
            <span class="share_u3">0</span class="share_u2"><span>元</span>
            <p>预计收益</p>
          </div>
        </div>
        <div class="share_mingxi">
          <h3 class="share_h32"><span class="share_span9"></span>我的受益</h3>
          <div class="share_jiangli">
            <div class="share_songcan">
              <img src="../assets/dongtaitu.gif" alt="">
            </div>
          </div>
          <h3 class="share_h33">没有奖励</h3>
          <p>你还没邀请过好友哦</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'share',
  data () {
    return {
      isShow:1
    }
  },
  created(){
    bus.$emit("isHide",true);
  },
  methods:{
    goback(){
        window.history.back();
        bus.$emit("isHide",false);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@import "../assets/Css/Share.css"
</style>


