<template>
  <div id="d_box">
    <Load v-show="isLoading"></Load>
    <div class="d_top">
      <div class="d_top1">
        <header class="d_topheader">
          <div class="d_topheader1">
            <div class="d_topheader1_back" @click="goback"></div>
            <h1 class="d_topheader1_title">{{titleName}}</h1>
          </div>
        </header>
        <div class="d_topfilter">
          <div class="d_topfilter1">
            <a class="d_topfilter1a">
              <span>分类</span>
            </a>
            <a class="d_topfilter1a">
              <span>综合排序</span>
            </a>
            <a class="d_topfilter1a">
              <span>筛选</span>
            </a>
          </div>
        </div>
      </div>
    </div>
    <ul class="d_foodlist">
      <div  v-for="(item,index) in foodList" v-if="foodList.length>0">
        <router-link class="d_shop_container" to="/shop" tag="section">
          <div class="d_shopinfo">
            <div class="d_shoplogo">
              <div class="d_shoplogoinner">
                <img class="logo-logo_3S1eSkn_0" :src="'https://fuss10.elemecdn.com/'+item.image_path.substr(0,1)+'/'+item.image_path.substr(1,2)+'/'+item.image_path.substr(3)+'?imageMogr/format/webp/thumbnail/!130x130r/gravity/Center/crop/130x130/'">
              </div>
            </div>
            <div class="d_inforight">
              <section class="d_inforight1">
                <h3 class="d_shopname">
                  <i class="d_pinpai">品牌</i>
                  <span class="d_ellipcis">{{item.name}}</span>
                </h3>
                <ul class="d_bao">
                  <li>保</li>
                  <li>票</li>
                </ul>
              </section>
              <section class="d_inforight1">
                <div class="d_count">
                  <span>月售{{item.recent_order_num}}单</span>
                </div>
                <span class="d_bird">蜂鸟专送</span>
              </section>
              <section class="d_inforight1">
                <div class="d_moneylimit">
                  <span>¥{{item.float_minimum_order_amount}}起送</span>
                  <span class="d_ellipcis">配送费{{item.float_delivery_fee}}¥</span>
                </div>
                <div class="d_time">
                  <span>{{(item.distance/1000).toFixed(2)}}km</span>
                  <span>{{item.order_lead_time}}分钟</span>
                </div>
              </section>
            </div>
          </div>
          <div class="d_shopdes">
            <section class="d_shopdes1">
              <h3 class="d_line"></h3>
            </section>
          </div>  
        </router-link>
      </div>
      <div  v-for="(item,index) in biaoList" v-if="biaoList.length>0">
        <router-link class="d_shop_container" to="/shop" tag="section">
          <div class="d_shopinfo">
            <div class="d_shoplogo">
              <div class="d_shoplogoinner">
                <img class="logo-logo_3S1eSkn_0" :src="'https://fuss10.elemecdn.com/'+item.restaurant.image_path.substr(0,1)+'/'+item.restaurant.image_path.substr(1,2)+'/'+item.restaurant.image_path.substr(3)+'?imageMogr/format/webp/thumbnail/!130x130r/gravity/Center/crop/130x130/'">
              </div>
            </div>
            <div class="d_inforight">
              <section class="d_inforight1">
                <h3 class="d_shopname">
                  <i class="d_pinpai">品牌</i>
                  <span class="d_ellipcis">{{item.restaurant.name}}</span>
                </h3>
                <ul class="d_bao">
                  <li>保</li>
                  <li>票</li>
                </ul>
              </section>
              <section class="d_inforight1">
                <div class="d_count">
                  <span>月售{{item.restaurant.recent_order_num}}单</span>
                </div>
                <span class="d_bird">蜂鸟专送</span>
              </section>
              <section class="d_inforight1">
                <div class="d_moneylimit">
                  <span>¥{{item.restaurant.float_minimum_order_amount}}起送</span>
                  <span class="d_ellipcis">配送费{{item.restaurant.float_delivery_fee}}¥</span>
                </div>
                <div class="d_time">
                  <span>{{(item.restaurant.distance/1000).toFixed(2)}}km</span>
                  <span>{{item.restaurant.order_lead_time}}分钟</span>
                </div>
              </section>
            </div>
          </div>
          <div class="d_shopdes">
            <section class="d_shopdes1">
              <h3 class="d_line"></h3>
            </section>
          </div>  
        </router-link>
      </div>
    </ul>
  </div>
</template>

<script>
import Load from "./loading"
export default {
  name: 'food',
  data () {
    return {
      foodList:[],
      titleName:"",
      isLoading:true,
      biaoList:[]
    }
  },
   components:{
    Load,
  }
  ,
  created(){
    this.isLoading=true;
    var index=this.$route.params.id;
    var biao=this.$route.query.id;
    console.log(biao)
    if (index==1) {
      this.titleName="美食"
    }else if(index==2){
      this.titleName="商超便利"
    }else if(index==3){
      this.titleName="果蔬生鲜"
    }
    if (biao==1) {
      this.titleName="大牌五折"
    }else if(biao==2){
      this.titleName="甜品饮品"
    }else if(biao==3){
      this.titleName="医药健康"
    }

    bus.$emit("isHide",true);
    setTimeout(()=>{
      if(index==1||index==2||index==3){
        this.$http("../../static/food"+index+".json").then((res)=>{
            this.isLoading=false;
            this.foodList=res.data;
             console.log(this.foodList)
        });  
      }
      if(biao==1||biao==2||biao==3){
        this.$http("../../static/biao"+biao+".json").then((res)=>{
            this.isLoading=false;
            this.biaoList=res.data.items;
            console.log(this.biaoList)
        });
      }
    },1500);
    
    
   
  },
  methods:{
    goback(){
        window.history.back();
        bus.$emit("isHide",false);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@import '../assets/Css/Food.css'
</style>


