<template>
  <div class="i_box">
    <!-- h -->
    <div class="i_header">
      <span @click="goback"></span>
      <p>账户信息</p>
    </div>
    <!-- 主体 -->
    <div class="i_main">
      <!-- 1 -->
      <div class="i_div1">
        <b>头像</b>
        <span class="i_span2">
          <img src="../assets/photo.png" v-show="isShow===0">
          <img src="../assets/cat.jpg" v-show="isShow===1" style="border-radius:50%">
        </span>
        <span class="i_span3"></span>
      </div>
      <!-- 2 -->
      <div class="i_div1 i_div2">
        <b>用户名</b>
        <span class="i_span22">
          {{username[0]}}
        </span>
        <span class="i_span3"></span>
      </div>
      <!-- 3 -->
      <div class="i_div3">
        <p>账号绑定</p>
      </div>
      <!-- 4 -->
      <div class="i_div1 i_div2">
        <b><i><img src="../assets/tel.png"></i>手机</b>
        <span class="i_span22">
          {{username[1]}}
        </span>
        <span class="i_span3"></span>
      </div>
      <!-- 5 -->
      <div class="i_div3">
        <p>安全设置</p>
      </div>
      <!-- 6 -->
      <div class="i_div1 i_div2">
        <span class="i_span00">登录密码</span>
        <span class="i_span22" style="color: #0097ff;">
          未设置
        </span>
        <span class="i_span3"></span>
      </div>
      <!-- 7 -->
      <div class="i_tcdl" @click="out">
        退出登录
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'inf',
  data () {
    return {
      username:[],
      isShow:0
    }
  },
  created(){
    bus.$emit("isHide",true);
    this.username=this.$store.state.username;
    if (this.$store.state.username.length>0) {
      this.isShow=1;
    };
  },
  methods:{
    goback(){
        window.history.back();
        bus.$emit("isHide",false);
    },
    out(){
      this.$store.state.username.length=0;
      this.$store.state.orderList.length=0;
      this.$store.state.notdone.length=0;
      this.$store.state.done.length=0;
      this.$router.push("/")
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@import "../assets/Css/Inf.css"
</style>



