<template>
    <div id="s_box">
        <section class="s_top">
            <span class="s_back" @click="goback"></span>
            <form action="" class="s_form">
                <input type="search" autofocus="autofocus" placeholder="输入商家、商品名称" class="s_form_input">
                <router-link class="s_form_button" to="shop" tag="button">搜索</router-link>
            </form>
        </section>
        <div class="s_hotsearch">
            <div class="s_hotsearch_inner">
            <section class="s_hotlist">
                <header class="s_hotlist_title">热门搜索</header>
                <section class="s_hotnames">
                <router-link class="s_hotname" to="shop" tag="span" v-for="(item,index) in foodName" :key="index">{{item.word}}</router-link>
                </section>
            </section>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  name: 'search',
  data () {
    return {
      foodName:[]
    }
  },
  created(){
    bus.$emit("isHide",true);
    this.$http("../../static/search.json").then((res)=>{
        this.foodName=res.data;
    })
  },
  methods:{
    goback(){
        window.history.back();
        bus.$emit("isHide",false);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@import '../assets/Css/Search.css'

</style>


