<template>
<div>
  <div id="p_box">
    <header class="p_header">
      <div class="p_header_inner">
        <div class="p_back" @click="goback"></div>
        <h1 class="p_checkorder">在线支付</h1>
      </div>
    </header>
    <div class="p_main">
      <div class="p_main_time p_tip">
        <p class="p_resttime">支付剩余时间</p>
        <span class="p_timer">{{time}}</span>
      </div>
    </div>
    <section class="p_info">
      <ul class="p_infotop">
        <li class="p_infoli">
          <span class="p_ordername p_ellipcis">粤粥坊 外卖订单</span>
          <span class="p_orderdetail">详情</span>
          <span class="p_ordermoney">¥&nbsp;{{totalCount+5}}</span>
        </li>
      </ul>
      <div class="p_payment">
        <section class="p_payment_container">
          <div class="p_paytitle">
            <span>选择支付方式</span>
          </div>
          <ul class="p_paytype">
            <li :class="{p_zhifubao:true,p_ways:true,p_selected:isSelected===0}" @click="isSelected=0">
              <div class="p_zfbicon"></div>
              <div class="p_zfbname">支付宝</div>
            </li>
            <li :class="{p_weixin:true,p_ways:true,p_selected:isSelected===1}" @click="isSelected=1" v-show="isShow===1">
              <div class="p_zfbicon2"></div>
              <div class="p_zfbname">微信支付</div>
            </li>
            <li class="p_otherpay" @click="isShow=1">
              <span class="p_moreway">其他支付方式</span>
              <span class="p_morela"></span>
            </li>
          </ul>
        </section>
      </div>
      <div class="p_submit">
        <div class="p_submitbtn">
          <button class="p_paybutton" @click="jupm(0)">确认支付 ¥{{totalCount+5}}</button>
        </div>
      </div>
    </section>
  </div>
  <div class="p_cover" v-show="isShow1===0">
    <p class="p_infoinfo">支付完成！</p>
    <p class="p_infoinfo1">感谢您的惠顾，默认跳转到首页....</p>
  </div>
</div>
</template>

<script>
export default {
  name: 'pay',
  data () {
    return {
      isShow:0,
      isGreen:0,
      isSelected:0,
      isShow1:1
    }
  },
  created(){
    bus.$emit("isHide",true);
    
  },
  mounted(){
     

  },
  methods:{
    goback(){
        window.history.back();
        bus.$emit("isHide",false);
    },
    jupm(pro){
      this.$store.dispatch("set_Time",pro);
      this.$store.state.notdone.length=0;
      this.$store.state.done.push([this.$store.state.orderList[0].name,this.$store.state.orderList.length,this.totalCount+5]);
      this.isShow1=0;
      this.$store.state.orderList.length=0;
      setTimeout(function(){
        window.location.hash="/"
        bus.$emit("isHide",false);
      },1500)
    }
  }
  ,
  computed:{
    totalCount(){
        return this.$store.getters.total;
    },
    time(){
      return this.$store.state.time;
    }

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@import '../assets/Css/Pay.css'
</style>\8


