<template>
  <div id="l_box">
    <div class="l_main">
      <div class="l_back" @click="goback"></div>
      <div class="l_top">
        <p class="l_top_logo"><img src="../assets/timg.jpg"></p>
        <div class="l_top_two">
          <a  :class="{l_blue:isBlue===0}" @click="isShow=0;isBlue=0">短信登陆</a>
          <a  :class="{l_blue:isBlue===1}" @click="isShow=1;isBlue=1">密码登陆</a>
        </div>
      </div>
      <div class="l_duanxin" v-show="isShow===0">
        <form action="" class="l_form">
          <section class="l_phone">
            <input type="tel" maxlength="11" placeholder="手机" class="l_phone_inner l_pnum">
            <p class="l_phone_right">获取验证码</p>
          </section>
          <section class="l_check">
            <input type="tel" maxlength="11" placeholder="验证码" class="l_phone_inner l_yz">
          </section>
          <section class="l_tipp">
            温馨提示：未注册饿了么帐号的手机号，登录时将自动注册，且代表您已同意
            <a href="javascript:;">《用户服务协议》</a>
          </section>
          <p class="l_btn">登陆</p>
        </form>
        <a class="l_aboutus">关于我们</a>
      </div>
      <div class="l_psd" v-show="isShow===1">
        <form action="" class="l_form">
          <section class="l_phone">
            <input type="tel" maxlength="11" placeholder="用户名" class="l_phone_inner">
          </section>
          <section class="l_check">
            <input :type="isType" maxlength="11" placeholder="密码" class="l_phone_inner">
            <div :class="{l_btnright:true,l_active:isAct===0}" @click="change">
              <div :class="{l_move:isAct===0}"></div>
              <i :class="{l_moveleft:isAct===0}" v-text="isTxt">...</i>
            </div>
          </section>
          <p class="l_btn">登陆</p>
        </form>
        <a class="l_aboutus">关于我们</a>
      </div>
      <p class="l_checkcheck">
      </p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'login',
  data () {
    return {
      isBlue:0,
      isShow:0,
      isAct:1,
      isTxt:"...",
      isType:"password"
    }
  },
  created(){
    bus.$emit("isHide",true);
  },
  methods:{
    goback(){
        window.history.back();
        bus.$emit("isHide",false);
    },
    change(){
      this.isAct==1?this.isAct=0:this.isAct=1;
      this.isAct==1?this.isTxt="...":this.isTxt="abc";
      this.isType==="password"?this.isType="text":this.isType="password"
      
    }
    
  },
  mounted(){
    // 验证1
    // 生成验证码
    var newStr;
    function yzm() {
      var str = "abcdefghijklmnopqrstuvwxyz1234567890";
          newStr = "";
      for (var i=1; i<=5; i++) {
        var index = parseInt(Math.random()*str.length);
        newStr += str[index];
      }
       $(".l_phone_right").text(newStr);
    }
    $(".l_phone_right").on("click",function(){
        $(".l_phone_right").css({"color":"#2395ff"})
        yzm();
    })
    function checkPhone(){
      if ($(".l_phone_inner")[0].value==""||$(".l_phone_inner")[0].value.trim()=="") {
        $(".l_checkcheck").text("手机号输入不能为空！");
         $(".l_checkcheck").fadeIn(2000);
        return false
      };
      var reg=/^13[1234567890]\d{8}$/;
      if (reg.test($(".l_phone_inner")[0].value)) {
        $(".l_checkcheck").text("手机号输入正确！");
        return true;
      }else{
        $(".l_checkcheck").text("手机号格式不正确！");
        $(".l_checkcheck").fadeIn(2000);
        return false;
      }
    }
    function checkYzm(){
      if ($(".l_phone_inner")[1].value==""||$(".l_phone_inner")[1].value.trim()=="") {
        $(".l_checkcheck").text("验证码输入不能为空！");
        $(".l_checkcheck").fadeIn(2000);
        return false
      };
      if ($(".l_phone_inner")[1].value===newStr) {
        $(".l_checkcheck").text("验证码输入正确！");
        return true;
      }else{
        $(".l_checkcheck").text("验证码输入有误！");
        $(".l_checkcheck").fadeIn(2000);
        return false;
      }

    }
    $(".l_phone_inner")[0].onblur=checkPhone;
    $(".l_phone_inner")[1].onblur=checkYzm;
    // 登陆1验证
    $(".l_btn").eq(0).on("click",()=>{

        var res1=checkPhone();
        var res2=checkYzm();
        if (res1&&res2) {
          var v=$(".l_phone_inner")[0].value;
          var phoneNum=v.substr(0,3)+"****"+v.substr(7);
          var name=v.substr(3,2)+newStr+v.substr(5,2);
          this.$store.state.username=[name,phoneNum];
          window.history.back();
        };
    })

    //  验证2
    function checkName(){
      if ($(".l_phone_inner")[2].value==""||$(".l_phone_inner")[2].value.trim()=="") {
        $(".l_checkcheck").text("用户名输入不能为空！");
        $(".l_checkcheck").fadeIn(2000);
        return false;
      };
      var reg=/^[a-zA-Z_]\w{5,11}$/;
      if (reg.test($(".l_phone_inner")[2].value)) {
        $(".l_checkcheck").text("用户名输入正确！");
        return true;
      }else{
        $(".l_checkcheck").text("用户名格式不正确！");
        $(".l_checkcheck").fadeIn(2000);
        return false;
      }
    }
    function checkPsd(){
      if ($(".l_phone_inner")[3].value==""||$(".l_phone_inner")[3].value.trim()=="") {
        $(".l_checkcheck").text("密码输入不能为空！");
        $(".l_checkcheck").fadeIn(2000);
        return false
      };
      if ($(".l_phone_inner")[3].value==="abc123") {
         $(".l_checkcheck").text("密码输入正确！");
        return true;
      }else{
        $(".l_checkcheck").text("密码输入有误！");
        $(".l_checkcheck").fadeIn(2000);
        return false;
      }
    }
    $(".l_phone_inner")[2].onblur=checkName;
    $(".l_phone_inner")[3].onblur=checkPsd;
    // 登陆2验证
    $(".l_btn").eq(1).on("click",()=>{
        var res1=checkName();
        var res2=checkPsd();
        if (res1&&res2) {
          this.$store.state.username=[$(".l_phone_inner")[2].value,"135****6637"];
          window.history.back();
        };
    })
    


  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@import '../assets/Css/Login.css'
</style>



