                    <div class="m_xx1">
                        <span class="m_span1">
                            <img src="../assets/cat.jpg" v-show="isShow===1">
                        </span>
                            <div class="m_ppp">
                                <a @click="goLogin">
                                    <p class="m_p1">{{username[0]}}</p>
                                </a>
                                <p class="m_p2">
                                    <span>{{username[1]}}</span>
                                </p>
                            </div>
                        <span class="m_span2"></span>
                    </div>
