</template>

<script>
export default {
  name: 'mine',
  data () {
    return {
        username:["登录/注册","登录后享受更多特权"],
        isShow:0

    }
  },
  created(){
    bus.$emit("isHide",false);
    bus.$emit("isBlue",3);
    if(this.$store.state.username.length>0){
        this.isShow=1;
        this.username=this.$store.state.username;
    }
    
  },
  methods:{
    goLogin(){
        if(this.$store.state.username.length==0){
            this.$router.push("/login");
        }
    },
    goInfo(){
        if(this.$store.state.username.length==0){
            this.$router.push("/login");
        }else{
            this.$router.push("/inf");
        }

    }


  }
}
</script>
