<template>
    <div>
            <!-- h -->
            <div class="o_header">
                <p>订单</p>
            </div>
            <!-- nav -->
            <div class="o_main">
                <!-- 订单取消 -->
                <!-- 未支付 -->
                <div class="o_dd1" v-for="item in notdone">
                    <div class="o_dd2">
                        <a href="###">
                            <img src="https://fuss10.elemecdn.com/9/ea/df22dbff554fa2b7dbce59fe90c2bjpeg.jpeg?imageMogr/format/webp/thumbnail/!64x64r/gravity/Center/crop/64x64/" alt="">
                        </a>
                        <div class="o_clcw">
                            <div class="o_pp">
                                <p class="o_p1"><span class="o_span1">粤粥坊</span><span class="o_span2">等待支付</span></p>
                                <span class="o_span3">3小时前</span>
                            </div>
                            
                            <p class="o_p2">
                                <span class="o_span4">{{item[0]}}</span ><span class="o_span6">等{{item[1]}}件商品</span>
                                <span class="o_span5"> ¥{{item[2]}}</span>
                            </p>
                        </div>
                    </div>
                    <div class="o_dd3">
                        <router-link style="color:#ff7223;border:1px solid pink" class="o_pay" to="pay" tag="button">去支付(还剩{{time}})</router-link>
                    </div>
                </div>
                <!-- 已完成 -->
                <div class="o_dd1" v-for="item in done">
                    <div class="o_dd2">
                        <a href="###">
                            <img src="https://fuss10.elemecdn.com/9/ea/df22dbff554fa2b7dbce59fe90c2bjpeg.jpeg?imageMogr/format/webp/thumbnail/!64x64r/gravity/Center/crop/64x64/" alt="">
                        </a>
                        <div class="o_clcw">
                            <div class="o_pp">
                                <p class="o_p1"><span class="o_span1">粤粥坊</span><span class="o_span2">订单已完成</span></p>
                                <span class="o_span3">19分钟前</span>
                            </div>
                            
                            <p class="o_p2">
                                <span class="o_span4">{{item[0]}}</span ><span class="o_span6">等{{item[1]}}件商品</span>
                                <span class="o_span5"> ¥{{item[2]}}</span>
                            </p>
                        </div>
                    </div>
                    <div class="o_dd3">
                        <router-link class="o_again" to="shop" tag="button">再来一单</router-link>
                    </div>
                </div>
            <section class="o_empty" v-show="isShow===1">
                <img src="//fuss10.elemecdn.com/f/18/9fb04779371b5b162b41032baf5f3gif.gif">
                <h3>登录后查看外卖订单</h3>
            </section>
            <!-- 页脚 -->
            <div class="o_ckdd" v-show="isShow===0">
                <div class="o_sanyue">查看三个月前的外卖订单 </div>
            </div>
            </div>
        </div>
</template>

<script>
export default {
  name: 'order',
  data () {
    return {
        done:[],
        notdone:[],
        isShow:1
    }
  },
  created(){
    bus.$emit("isBlue",2);
    this.done=this.$store.state.done
    this.notdone=this.$store.state.notdone;
    if (this.done.length>0||this.notdone.length>0) {
        this.isShow=0;
    };
  },
  computed:{
    time(){
        return this.$store.state.time;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>


