<template>
  <div id="p_box">
    <header class="po_header">
      <div class="po_header_inner">
        <div class="po_back" @click="goback"></div>
        <h1 class="po_checkorder">选择收货地址</h1>
        <span class="po_newadd">新增地址</span>
      </div>
    </header>
    <form action="" class="po_addform">
      <img src="../assets/search.png" alt="">
      <input type="text" class="po_addinput" placeholder="请输入地址" id="search">
    </form>
    <section class="po_curradd">
      <h4>当前地址</h4>
      <div class="po_adds">
        <span class="po_adds1">东城区北京市政府</span>
        <span class="po_dingwei">
          <i></i>
          <span>重新定位</span>
        </span>
      </div>
    </section>
    <section class="po_getadd">
      <h4>收货地址</h4>
      <div class="po_getadds">
        <p>
          <span class="po_addname">刘鹿</span>
          <span class="po_addsex">女士</span>
          <span class="po_addphone">13678965678</span>
        </p>
        <p class="po_adddown">东城区北京市政府</p>
      </div>
    </section>
    <section id="mapbox">
      <div id="map"></div>
      
    </section>
  </div>
</template>

<script>
export default {
  name: 'poi',
  data () {
    return {
      poi:{}
    }
  },
  created(){
    bus.$emit("isHide",true);
     this.$http("../../static/poi.json").then((res)=>{
        this.poi=res.data;
    });
  },
  methods:{
    goback(){
        window.history.back();
        bus.$emit("isHide",false);
    }
  },
  mounted(){
    // 创建地图
    var map = new AMap.Map('map',{
        zoom: 15,
        pitch:80,
        expandZoomRange:true,
        zooms:[3,20]
      });
    // 创建控件
    var scale =new AMap.Scale(),
    overView=new AMap.OverView(),
    toolBar=new AMap.ToolBar();
    // 添加控件到地图
    map.addControl(scale);
    map.addControl(overView);
    map.addControl(toolBar);
    // h5提供的navigator.geolocation接口
    if(navigator.geolocation){
      // 使用getlocation.getCurrentPosition获取当前位置信息
      navigator.geolocation.getCurrentPosition(function  (poi) {
        var marker=new AMap.Marker({
          map:map,
          // 位置经纬度
          position:[poi.coords.longitude,poi.coords.latitude]
        });   
    },function(error){
      console.log(error)
    });
    }

  }


}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@import '../assets/Css/Poi.css'
</style>


