<template>
  <div id="c_box">
    <header class="c_header">
      <div class="c_header_inner">
        <div class="c_back" @click="goback"></div>
        <h1 class="c_checkorder">确认订单</h1>
      </div>
    </header>
    <div class="c_viewmain">
      <div>
        <div class="c_maininner">
          <section class="c_add">
            <div class="c_addinner">
              <h2 class="c_add_name">
                <b>刘鹿</b>
                <span class="c_sex">女士</span>
                <span class="c_phonenum">13678965678</span>
              </h2>
              <p class="c_add_detail">
                <span class="c_add_tag">公司</span>
                东城区北京市政府306
              </p>
            </div>
          </section>
          <section class="c_detime">
            <div class="c_delivery">
              <div class="c_deliveryleft">
                <div class="c_delefttime">
                  <svg class="c_timeicon"></svg>
                  <span>送达时间</span>
                </div>
              </div>
              <div class="c_deliveryright">
                <span>尽快送达|预计19:21</span>
              </div>
            </div>
            <div class="c_deliverybird">
              <span class="c_fengniao">蜂鸟专送</span>
            </div>
          </section>
          <section class="c_foodlist">
            <h3 class="c_shopname">
              <i class="c_shopnameleft"></i>
              <span class="c_shoptitle c_ellipcis">粤粥坊（崇文门新世界店）</span>
              <i class="c_shopnameright"></i>
            </h3>
            <ul class="c_listmain">
              <li class="c_fooditem" v-for="(food,index) in orderList" :key="index">
                <div class="c_fooditem1">
                  <p class="c_ellipcis">{{food.name}}</p>
                </div>
                <span class="c_fooditem2">{{food["is_featured"]}}份</span>
                <span class="c_fooditem3">¥{{food["is_featured"]*food.specfoods[0].price}}</span>
              </li>
            </ul>
            <ul class="c_other">
              <li class="c_otherlist">
                <div>配送费</div>
                <div>¥5</div>
              </li>
              <li class="c_otherlist">
                <div>餐盒</div>
                <div>¥5</div>
              </li>
            </ul>
            <div class="c_redbag">
              <b>红包</b>
              <span class="c_redbagdes c_ellipcis">
                <span>
                  暂时只在饿了么 APP 中支持
                </span>
              </span>
            </div>
            <div class="c_sum">
              <div class="c_suminner">
                <div class="c_suminfo">
                  <a href="">
                    <span>优惠说明</span>
                  </a>
                </div>
                <div>
                  小计
                  <span class="c_pay">¥{{totalCount+5}}</span>
                </div>
              </div>
            </div>
          </section>
          <section class="c_onlinepay">
            <div class="c_payway">
              <b>在线支付</b>
            </div>
          </section>
          <div>
            <section class="c_ordertip">
              <a href="">
                <div class="c_cartitem">
                  <span class="c_ellipcis">订单备注</span>
                  <span class="c_cartitemright c_ellipcis">
                    <span>口味偏好、餐具份数等</span>
                  </span>
                </div>
              </a>
              <div class="c_cartitem c_paper">
                <div class="c_paperinner">
                  <div class="c_ppp">
                    <span>不需要开发票</span>
                  </div>
                </div>
              </div>
            </section>
          </div>
        </div>
        <footer class="c_footer">
          <span>待支付¥{{totalCount+5}}</span>
          <button class="c_paybtn"><a  @click="notdone(1)">去支付</a></button>
        </footer>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'count',
  data () {
    return {
      orderList:[],
      flag:true
    }
  },
  created(){
    bus.$emit("isHide",true);
     this.orderList=this.$store.state.orderList;
  },
  methods:{
    goback(){
      window.history.back();
    },
    notdone(pro){
      this.$store.dispatch("set_Time",pro);
      this.$store.state.notdone.length=0;
      this.$store.state.notdone.push([this.$store.state.orderList[0].name,this.$store.state.orderList.length,this.totalCount+5]);
      this.$router.push("/pay");
    }
  },
  computed:{
    totalCount(){
        return this.$store.getters.total;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@import '../assets/Css/Count.css'
</style>


