<template>
  <div id="app">

    <div id="box">
        <router-view/>
    </div>
    
    <footer :class="{h_footer:true,hide:isHide}">
        <div class="h_footer_inner">
            <router-link :class="{h_footer_innera:true,h_footeractive:isBlue===0}" to="/">
                <span class="h_downicon h_downicon1"></span>
                <span>外卖</span>
            </router-link>
            <router-link :class="{h_footer_innera:true,h_footeractive:isBlue===1}" to="find">
                <span class="h_downicon h_downicon2"></span>
                <span>发现</span>
            </router-link>
            <router-link :class="{h_footer_innera:true,h_footeractive:isBlue===2}" to="order">
                <span class="h_downicon h_downicon3"></span>
                <span>订单</span>
            </router-link>
            <router-link :class="{h_footer_innera:true,h_footeractive:isBlue===3}" to="mine">
                <span class="h_downicon h_downicon4"></span>
                <span>我的</span>
            </router-link>
        </div>
    </footer>
  </div>
</template>

<script>
export default {
  name: 'app',
  data(){
    return {
        isHide:false,
        isBlue:0
    }
  },
  created(){
    var _t=this;
    bus.$on("isHide",function(flag){
        _t.isHide=flag;

    });
    bus.$on("isBlue",function(flag){
        _t.isBlue=flag;

    });
  
  }
 

}
</script>

<style>
@import './assets/Css/public.css'
</style>



